.sec2 {
    background-color: #fff;
    text-align: center;

    .sec-title {
        font-size: 1.5rem;
        color: #777777;

        @media screen and(min-width: 768px) and (max-width: 999px) {
            font-size: 2.5rem;
        }

        @include mq(md) {
            font-size: 3.5rem;
        }
    }

    .look {
        padding-top: 1.5rem;
        font-size: 1.4rem;
        padding-bottom: 1rem;

        @include mq(md) {
            padding-top: 2rem;
            font-size: 2rem;
            padding-bottom: 2rem;
        }
    }

    .video {
        width: 100%;
        height: auto;
        padding-bottom: 2.5rem;

        @include mq(md) {
            width: 70%;
            margin: 0 auto;
            padding-bottom: 7rem;
        }
    }

    .sec2-sub {
        color: #777777;
        font-size: 1.5rem;
        padding-bottom: 2rem;

        @include mq(md) {
            font-size: 3.5rem;
            padding-bottom: 2.5rem;
        }
    }
}