@charset "UTF-8";
*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

/*PC表示のみ*/
.pc-only {
  display: none;
}

@media screen and (min-width: 768px) {
  .pc-only {
    display: block;
  }
}

/*SP表示のみ*/
@media screen and (min-width: 768px) {
  .sp-only {
    display: none;
  }
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Helvetica Neue', 'Helvetica', 'Arial', 'Hiragino Sans', 'ヒラギノ角ゴシック', YuGothic, 'Yu Gothic', 'メイリオ', Meiryo, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
  font-size: 1.2rem;
  line-height: 1.7;
  letter-spacing: 0.05em;
  padding: 10px;
}

/*　枠　*/
html:before,
html:after,
body:before,
body:after {
  content: "";
  background: #000;
  position: fixed;
  display: block;
  z-index: 1;
}

/* 上 */
html:before {
  height: 10px;
  width: 100vw;
  left: 0;
  top: 0;
}

/* 右 */
html:after {
  width: 10px;
  height: 100vh;
  right: 0;
  top: 0;
}

/* 下 */
body:before {
  height: 10px;
  width: 100vw;
  bottom: 0;
  left: 0;
}

/* 左 */
body:after {
  width: 10px;
  height: 100vh;
  top: 0;
  left: 0;
}

/*各セクションの余白*/
.sec-block {
  padding: 40px 0;
}

