.sec3 {
    width: 100%;
    // height: 0;
    // min-height: 450px;
    // padding-top: 50.7%;
    background-image: url('../dest/img/trading.png');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    position: relative;

    @include mq(md) {
        min-height: 700px;
    }

    @media screen and(min-width: 1550px) {
        min-height: 800px;
    }

    .sec3-content {
        @include mq(md) {
            width: 70%;
            margin: 0 auto
        }

        .sec3-title {
            font-size: 1.75rem;

            @media screen and (min-width: 600px) {
                text-align: center;
                font-size: 1.8rem;
            }

            @include mq(md) {
                text-align: center;
                font-size: 2.4rem;
            }

            @media screen and(min-width: 1550px) {
                font-size: 3.2rem;
            }
        }

        .sec-3-text {
            padding: 2.5rem 0;

            @media screen and (min-width: 480px) and(max-width: 599px) {
                font-size: 1.3rem;
            }

            @media screen and (min-width: 600px) {
                font-size: 1.5rem;
            }

            @media screen and (min-width: 786px) and (max-width: 999px) {
                padding: 1.5rem 0;
            }

            @include mq(md) {
                padding-top: 1.7rem;
                font-size: 2rem;
            }

            @media screen and(min-width: 1550px) {
                padding-top: 2.5rem;
                font-size: 2.2rem;
            }
        }

        .imp-message {
            display: flex;
            justify-content: center;

            .kakko-left {
                width: 15%;
                height: 10%;
                padding-top: 11rem;
            }

            .okashi {
                text-align: center;
                font-size: 1.8rem;
                padding-top: 15%;

                // @media screen and (min-width: 480px) {
                //     padding-top: 1rem;
                //     font-size: 2.4rem;
                // }

                @media screen and (min-width: 480px) {
                    padding-top: 15%;
                    font-size: 2.2rem;
                }

                @media screen and (min-width: 568px) {
                    font-size: 2.2rem;
                    padding-top: 15%;
                }

                // @media screen and (min-width: 600px) {
                //     padding-top: 2rem;
                //     font-size: 2.8rem;
                // }

                @media screen and (min-width: 769px) {
                    font-size: 2.3rem;
                    padding-top: 10%;
                }

                @media screen and (min-width: 1000px) {
                    font-size: 3rem;
                }

                @media screen and (min-width: 1550px) {
                    padding-top: 10%;
                    font-size: 3.5rem
                }
            }

            .kakko-right {
                width: 15%;
                height: 10%;
                padding-bottom: 11rem;
            }

        }

    }
}