*,
*::after,
*::before {
    box-sizing: border-box;
}

img {
    max-width: 100%;
}

/*PC表示のみ*/
.pc-only {
    display: none;

    @include mq(md) {
        display: block;
    }
}

/*SP表示のみ*/
.sp-only {
    @include mq(md) {
        display: none;
    }
}

html {
    font-size: 62.5%;
}

body {
    font-family: 'Helvetica Neue', 'Helvetica', 'Arial', 'Hiragino Sans', 'ヒラギノ角ゴシック', YuGothic, 'Yu Gothic', 'メイリオ', Meiryo, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
    font-size: 1.2rem;
    line-height: 1.7;
    letter-spacing: 0.05em;
    padding: 10px;
}

/*　枠　*/
html:before,
html:after,
body:before,
body:after {
    content: "";
    background: #000;
    position: fixed;
    display: block;
    z-index: 1;
}

/* 上 */
html:before {
    height: 10px;
    width: 100vw;
    left: 0;
    top: 0;
}

/* 右 */
html:after {
    width: 10px;
    height: 100vh;
    right: 0;
    top: 0;
}

/* 下 */
body:before {
    height: 10px;
    width: 100vw;
    bottom: 0;
    left: 0;
}

/* 左 */
body:after {
    width: 10px;
    height: 100vh;
    top: 0;
    left: 0;
}

/*各セクションの余白*/
.sec-block {
    padding: 40px 0;
}

/* メール入力フォーム */
.common-form-1 {
    padding: .5rem 0;
    width: 100%;

    @media screen and (min-width: 600px) {
        width: 70%;
    }

    @media screen and(min-width: 768px) and (max-width: 999px) {
        padding: 1rem;
    }


    @media screen and(min-width: 1000px) {
        max-width: 60%;

    }


    .common-input {
        @include common-input;
    }

    .common-input-2 {
        @include common-input-2;
    }

    .button-style {
        @include button-style;
    }

    .button-style-2 {
        @include button-style-2;
    }
}

.common-form-2 {
    padding: 1rem 0;

    @include mq(md) {
        padding: 3rem 0;
        max-width: 60%;
        font-size: 2rem;
        margin: 0 auto;
    }

    .common-input {
        @include common-input;
    }

    .common-input-2 {
        @include common-input-2;
    }

    .button-style {
        @include button-style;
    }

    .button-style-2 {
        @include button-style-2;
    }
}

/*プライバシーポリシー*/
.privacy-policy-1 {
    font-size: .7rem;
    text-align: left;
    letter-spacing: 0;
    line-height: 1.4;

    @include mq(md) {
        font-size: 1rem;
        // max-width: 60%;
        width: 60%;
    }
}

.privacy-policy-2 {
    font-size: .7rem;
    text-align: left;
    letter-spacing: 0;
    line-height: 1.5;

    @include mq(md) {
        font-size: 1rem;
        margin: 0 auto;
        // max-width: 50%;
        width: 50%;
        text-align: left;
    }

    @media screen and(min-width: 768px) and (max-width: 999px) {
        // max-width: 80%;
        width: 60%;
    }


}